.alph_tooltip {
    position: relative;
    display: inline-block;
}

.alph_tooltip .tooltiptext {
    visibility: hidden;
    position: absolute;
    width: 120px;
    background-color: #27323b;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
    z-index: 1;
    opacity: 0;
    transition: opacity .6s;

    font-size: 12px;
    display: none;
}
.alph_tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
    display: inline;
}

.alph_tooltip-top {
    bottom: 125%;
    left: 50%;
    margin-left: -50%;
}

.alph_tooltip-top::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.alph_tooltip-top-right {
    bottom: 125%;
    right: 0;
    margin-left: -50%;
}

.alph_tooltip-top-right::after {
    content: "";
    position: absolute;
    top: 100%;
    right: 15%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.alph_tooltip-top-left {
    bottom: 125%;
    left: 0;
}

.alph_tooltip-top-left::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    margin-left: 5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.alph_tooltip-right {
    top: -5px;
    left: 125%;
}

.alph_tooltip-right::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent #555 transparent transparent;
}

.alph_tooltip-bottom {
    top: 135%;
    left: 50%;
    margin-left: -50%;
}

.alph_tooltip-bottom-wide {
    top: 135%;
    left: -30%;
}

.alph_tooltip-bottom-narrow {
    top: 135%;
    left: -75%;
}

.alph_tooltip-bottom-narrow2 {
    top: 135%;
    left: -165%;
}

.alph_tooltip-bottom::after,
.alph_tooltip-bottom-wide::after,
.alph_tooltip-bottom-narrow::after,
.alph_tooltip-bottom-narrow2::after  {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #555 transparent;
}

.alph_tooltip-bottom-right {
    top: 135%;
    right: 0;
    margin-left: -50%;
}

.alph_tooltip-bottom-right::after {
    content: "";
    position: absolute;
    bottom: 100%;
    right: 15%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #555 transparent;
}

.alpheios-panel-right {
    .alph_tooltip-bottom-right {
        top: 135%;
        right: auto;
        margin-left:0;
        left: 0
    }
    .alph_tooltip-bottom-right::after {
        margin-left:0;
        right: auto;
        left: 15%;
    }
}

.alph_tooltip-left {
    top: -5px;
    bottom: auto;
    right: 128%;
}

.alph_tooltip-left::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent transparent #555;
}