.alpheios-panel__tab-panel.alpheios-panel__tab__inflections {
  padding: 0 0 20px; }

.alpheios-inflections__placeholder {
  padding: 0 20px;
  margin-bottom: 1rem; }

.alpheios-inflections__content {
  padding: 0 20px;
  border-bottom: 1px solid #d3d3d3; }

h3.alpheios-inflections__title {
  line-height: 1;
  margin: 0 0 0.6rem 0;
  font-weight: 700;
  text-align: center; }

h4.alpheios-inflections__additional_title {
  line-height: 1.6;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.6rem 0; }

.auk .alpheios-select.alpheios-inflections__view-selector {
  height: auto !important;
  max-width: 220px;
  line-height: 1.6; }

.alpheios-inflections__actions {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 0.6rem;
  margin-top: 10px; }

.alpheios-inflections__form {
  font-weight: bold;
  line-height: 1.2;
  justify-content: flex-start; }

.alpheios-inflections__placeholder {
  text-align: center; }

.alpheios-inflections__footnotes {
  display: none;
  grid-template-columns: 1.6rem 1fr;
  font-size: 0.875rem;
  line-height: 1.2;
  margin-bottom: 2rem; }
  .alpheios-inflections__footnotes dt {
    font-weight: 700; }

[data-footnote] {
  position: relative;
  padding-left: 2px;
  vertical-align: super; }

.alpheios-inflections__credits-cont {
  margin-bottom: 10px; }

h3.alpheios-inflections__credits-title {
  font-size: 16px;
  font-weight: 700;
  color: #4E6476;
  margin-bottom: 0; }

.alpheios-inflections__credits-text {
  font-size: 12px;
  font-weight: normal;
  color: #7E8897;
  font-style: italic;
  padding: 5px; }

.alpheios-inflections__paradigms-expl {
  font-size: 12px;
  font-weight: normal;
  color: #7E8897;
  font-style: italic;
  margin: 20px 0 10px; }

.alpheios-inflections__paradigms-expl span {
  color: #4E6476;
  font-weight: 700; }

.alpheios-inflections__supp-tables {
  margin-top: 4rem; }

.alpheios-inflections__progress-wrapper {
  height: 1.2rem;
  margin: 0 1rem 2rem;
  font-size: 0.875rem; }

.alpheios-inflections__progress-border {
  border: 2px solid #73CDDE;
  min-width: 300px;
  height: 100%;
  padding: 2px; }

.alpheios-inflections__progress-whitespace {
  overflow: hidden;
  height: 100%;
  margin: 0 auto;
  position: relative; }

.alpheios-inflections__progress-line {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #73CDDE;
  animation: cssload-slide 5.75s steps(40) infinite; }

.alpheios-inflections__progress-whitespace div.alpheios-inflections__progress-text,
.alpheios-inflections__progress-text {
  text-transform: uppercase;
  color: #666666;
  position: absolute;
  width: 100%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 300px;
  font-size: 13px;
  top: 3px; }

@keyframes cssload-slide {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }

@-o-keyframes cssload-slide {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }

@-ms-keyframes cssload-slide {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }

@-webkit-keyframes cssload-slide {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }

@-moz-keyframes cssload-slide {
  0% {
    left: -100%; }
  100% {
    left: 100%; } }
