



































































































































































































































































































































































































































































































@import "../../styles/variables";

.alpheios-panel {
  z-index: 2000;
  position: fixed;
  resize: both;
  opacity: 0.95;
  direction: ltr;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: px2rem(56px) auto px2rem(56px);
  grid-template-areas: "header" "content" "content";
}

.alpheios-panel[data-notification-visible="true"] {
  grid-template-areas: "header" "content" "notifications"
}

.alpheios-panel__header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  box-sizing: border-box;
  grid-area: header;
  background: var(--alpheios-color-muted);
}

.alpheios-panel__header-selection {
  font-size: 16px;
  font-weight: 700;
  color: $alpheios-toolbar-color;
}

.alpheios-panel__header-word {
  font-size: 14px;
  position: relative;
  top: -1px;
}

.alpheios-panel__close-btn {
  width: px2rem(60px);
  height: 100%;
  cursor: pointer;
  fill: var(--alpheios-color-neutral-lightest);
  stroke: var(--alpheios-color-neutral-lightest);
  stroke-width: 2.5;

  svg {
    position: relative;
    left: px2rem(8px);
    width: px2rem(44px);
    height: auto;
    top: 50%;
    transform: translateY(-50%);
  }

  &:hover,
  &:focus {
    fill: var(--alpheios-color-bright-hover);
    stroke: var(--alpheios-color-bright-hover);
    background: var(--alpheios-color-muted-hover);
  }

  &:active {
    fill: var(--alpheios-color-bright-pressed);
    stroke: var(--alpheios-color-bright-pressed);
    background: var(--alpheios-color-muted-pressed);
  }
}

.alpheios-panel__content {
  overflow: auto;
  grid-area: content;
  direction: ltr;
  box-sizing: border-box;
  display: flex;
  flex-flow: wrap;
  // Need to set element as an offset parent for panel content items
  position: relative;
  background: var(--alpheios-color-neutral-lightest);
  padding-top: px2rem(20px);
}

.alpheios-lookup__panel {
  display: block;
  border-bottom: 1px solid;
  margin-bottom: 20px;
}

.alpheios-panel__notifications, .alpheios-panel_notifications-auth {
  display: none;
  position: relative;
  padding: 10px 20px;
  background: $alpheios-logo-color;
  grid-area: notifications;
  overflow: hidden;
}

.alpheios-panel__notifications-close-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  display: block;
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  fill: $alpheios-link-color-dark-bg;
  stroke: $alpheios-link-color-dark-bg;
}

.alpheios-panel__notifications-close-btn:hover,
.alpheios-panel__notifications-close-btn:focus {
  fill: $alpheios-link-hover-color;
  stroke: $alpheios-link-hover-color;
}

.alpheios-panel__notifications--lang-switcher {
  font-size: 12px;
  float: right;
  margin: -20px 10px 0 0;
  display: inline-block;
}

.alpheios-panel__notifications--lang-switcher .alpheios-select {
  width: 120px;
  height: 25px;
}

.alpheios-panel__notifications--important {
  background: $alpheios-icon-color;
}

[data-notification-visible="true"] .alpheios-panel__notifications {
  display: block;
}

[data-notification-auth-visible="true"] .alpheios-panel__notifications-auth {
  display: block;
}

.alpheios-panel__tab-panel {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 100%;
}

.alpheios-panel__tab-panel--fw {
  width: 100%;
}

.alpheios-panel__tab-panel--no-padding {
  padding: 0;
}

.alpheios-panel__content_no_top_padding {
  padding-top: 0;
}

.alpheios-panel__message {
  margin-bottom: 0.5rem;
}

.alpheios-panel__options-item {
  margin-bottom: 0.5rem;
  max-width: 300px;
}

.alpheios-panel__contentitem {
  margin-bottom: 1em;
}

.alpheios-panel__header-btn-group--center {
  direction: ltr;
  display: flex;
  flex-wrap: nowrap;
  box-sizing: border-box;
  height: 100%;
}

.alpheios-panel__header-btn-group--end {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  box-sizing: border-box;
  height: 100%;
}

.alpheios-panel__tab__options {
  width: 100%;
  max-width: 600px;
}

.alpheios-panel__options-item {
  max-width: none;
  width: 100%;
}

.alpheios-panel__options-item .alpheios-select:not([multiple]):not([size]) {
  max-width: 235px;
  display: inline-block;
  vertical-align: top;
}

.alpheios-panel__options-item .alpheios-setting__label {
  width: 100px;
  display: inline-block;
}

.alpheios-panel__options-item select {
  display: inline-block;
}

.alpheios-panel__tab__inflections {
  width: 100%;
}

.alpheios-panel__tab__inflectionsbrowser {
  width: 100%;
}

.alpheios-panel__menu-btn {
  width: 40px;
  height: 40px;
  margin: 10px 10px 10px 30px;
  cursor: pointer;
}

.alpheios-panel__menu-icon {
  width: 40px;
  height: 40px;
  fill: $alpheios-link-color-dark-bg;
}

.alpheios-panel__menu-icon:hover,
.alpheios-panel__menu-icon:focus {
  fill: $alpheios-link-hover-color;
}

// Special styles for compact panel
.alpheios-panel--compact {
  height: 50vh;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid $alpheios-link-color-dark-bg;

  & .alpheios-panel__content {
    overflow: auto;
  }
}
