.alpheios_word_usage_list_item {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
  border-bottom: 1px solid #d3d3d3; }

.alpheios_word_usage_list_item__text {
  display: flex;
  flex-direction: row;
  line-height: normal;
  justify-content: center;
  align-items: center; }

.alpheios_word_usage_list_item
.alpheios_word_usage_list_item__text
.alpheios_word_usage_list_item__text_targetword {
  color: #E49A0D; }

.alpheios_word_usage_list_item__text_targetword {
  padding: 0 10px;
  font-weight: 700;
  text-align: center; }

.alpheios_word_usage_list_item__text_prefix {
  text-align: center;
  padding-left: 4px; }

.alpheios_word_usage_list_item__text_suffix {
  text-align: center; }

.alpheios_word_usage_list_item
.alpheios_word_usage_list_item__text .alpheios_word_usage_list_item__text_prefix,
.alpheios_word_usage_list_item
.alpheios_word_usage_list_item__text .alpheios_word_usage_list_item__text_suffix {
  font-size: 85%;
  width: 40%; }

.alpheios_word_usage_list_item__source-data {
  width: 100%;
  padding-top: 10px; }

.alpheios_word_usage_list_item
a.alpheios_word_usage_list_item__source_link {
  color: #3E8D9C;
  font-size: 95%;
  display: block; }

.alpheios_word_usage_list_item__source-link {
  display: inline-block;
  cursor: pointer; }

.alpheios_word_usage_list_item .alpheios_word_usage_list_item__source-data-title {
  display: block;
  width: 100%;
  padding-bottom: 5px; }

.alpheios_word_usage_list_item .alpheios_word_usage_list_item__text
.alpheios_word_usage_list_item__source {
  display: none; }

.alpheios-panel--compact
.alpheios_word_usage_list_item .alpheios_word_usage_list_item__source-data-title {
  display: none; }

.alpheios-panel--compact
.alpheios_word_usage_list_item .alpheios_word_usage_list_item__text
.alpheios_word_usage_list_item__source {
  display: inline-block; }
