.alpheios-popup-lexdataloading__progress-wrapper {
  box-sizing: border-box; }

.alpheios-popup-lexdataloading__progress-inner {
  border: 2px solid #73CDDE;
  border-radius: 0.625rem;
  text-align: center;
  box-sizing: border-box;
  position: relative;
  background: white; }

.alpheios-popup-lexdataloading__progress-line {
  -webkit-animation: progress 3s infinite;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 0.625rem; }

.alpheios-popup-lexdataloading__progress-text {
  display: block;
  padding: 4px;
  box-sizing: border-box;
  text-transform: uppercase;
  position: relative;
  z-index: 10;
  border: 2px solid #fff;
  border-radius: 0.625rem;
  font-size: 85%; }

@-webkit-keyframes progress {
  0% {
    background: #b1e3ed;
    width: 0%; }
  100% {
    background: #73CDDE;
    width: 100%; } }

@keyframes progress {
  0% {
    background: #b1e3ed;
    width: 0%; }
  100% {
    background: #73CDDE;
    width: 100%; } }

@-o-keyframes progress {
  0% {
    background: #b1e3ed;
    width: 0%; }
  100% {
    background: #73CDDE;
    width: 100%; } }

@-ms-keyframes progress {
  0% {
    background: #b1e3ed;
    width: 0%; }
  100% {
    background: #73CDDE;
    width: 100%; } }

@-moz-keyframes progress {
  0% {
    background: #b1e3ed;
    width: 0%; }
  100% {
    background: #73CDDE;
    width: 100%; } }
