.alpheios-info {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 16px;
  color: #666666;
  margin-top: 10px;
  margin-bottom: 10px; }

.alpheios-info .alpheios-info__helptext p {
  font-size: 14px;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 16px;
  color: #666666; }

.alpheios-info .alpheios-info__versiontext {
  font-size: 10.8px; }

.alpheios-info__currentlanguage {
  font-size: 10.8px;
  font-weight: bold; }

.alpheios-info__helptext {
  margin-top: 1em; }
