



























































































































































































































































@import "../../../styles/variables";

.alpheios-overlay-nav {
  background: transparent;
  position: fixed;
  top: 10px;
  right: 15px;
  z-index: 10000;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;

  .alpheios-navbuttons__btn {
    background: #FFF;
    border: 1px solid $alpheios-sidebar-header-border-color;
    padding: 5px;
  }

  .alpheios-navbuttons__btn {
    width: 40px;
    height: 40px;
    margin: 10px 0;
    box-sizing: border-box;
    position: relative;
    padding: 0;

    svg {
      width: 20px;
      height: 20px;
      top: 9px;
      left: 0;
      position: relative;
      box-sizing: border-box;
    }
  }

  .alph_tooltip {
    position: relative;

    .tooltiptext {
      visibility: hidden;
      position: absolute;
      width: 120px;
      text-align: center;
      z-index: 1;
      opacity: 0;
      font-size: 12px;
      display: none;
      padding: 5px 0;
      border-radius: 6px;
      transition: opacity 0.6s ease 0s;
      background-color: rgb(252, 252, 250);
      color: rgb(78, 100, 118);
      border-width: 1px;
      border-style: solid;
      border-color: rgb(78, 100, 118);
      border-image: initial;
    }

    .alph_tooltip-left {
      top: 0;
      bottom: auto;
      right: 128%;
    }

    .alph_tooltip-left::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 100%;
      margin-top: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: transparent transparent transparent #555;
    }
  }

  .lph_tooltip-left {
    top: 5px;
    bottom: auto;
    right: 128%;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 100%;
      margin-top: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: transparent transparent transparent #555;
    }
  }

  .alph_tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
    display: inline;
  }

  .alpheios-overlay-nav__header {
    height: 20px;
    width: 40px;
    box-sizing: border-box;
    border: 1px solid $alpheios-sidebar-header-border-color;
    cursor: pointer;
    background: #FFF;

    svg {
      width: 20px;
      height: auto;
      position: relative;
      left: 9px;
      top: -5px;
      fill: $alpheios-sidebar-header-border-color;
    }

    &.expanded svg {
      top: 5px;
    }

    &:hover svg {
      fill: $alpheios-link-hover-color;
    }
  }

  .alpheios-overlay-nav__drag-handle {
    width: 40px;
    height: 30px;
    border: 1px solid $alpheios-sidebar-header-border-color;
    border-bottom: none;
    background: #FFF;
    box-sizing: border-box;
  }

  .alpheios-overlay-nav__logo-icon {
    width: 30px;
    height: auto;
    position: relative;
    top: 3px;
    left: 4px;
  }

  .alpheios-overlay-nav__lookup-control {
    cursor: pointer;
    background: #FFF;

    .alpheios-navbuttons__btn {
      height: 30px;
      margin: 0;
      border-bottom: none;

      svg {
        top: 4px;
      }
    }
  }

  .alpheios-overlay-nav__lookup {
    display: flex;
    position: absolute;
    width: 400px;
    height: 80px;
    background: #FFF;
    left: -400px;
    top: 0;
    border: 1px solid $alpheios-sidebar-header-border-color;
    border-right: none;
    box-sizing: border-box;

    .alpheios-lookup__form {
      margin-top: 30px;
    }

    input {
      width: 300px;
      box-sizing: border-box;
      line-height: 28px;
      box-shadow: none;
      padding: 0 10px;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
      font-size: 14px;
      height: 30px;
      border: 1px solid $alpheios-copy-color;

      &:focus {
        border-color: $alpheios-link-hover-color;
        outline: transparent;
      }
    }

    .alph_tooltip {
      display: inline-block;
    }

    button {
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
      font-size: 12px;
      line-height: 30px;
      background-color: $alpheios-toolbar-color;
      color: #FFF;
      text-transform: uppercase;
      border-radius: 0;
      border: none;
      height: 30px;
      cursor: pointer;
    }
  }
}
