.alpheios-popup {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  min-height: 150px;
  z-index: 1000;
  position: fixed;
  left: 200px;
  top: 100px;
  // Required for Interact.js to take element size with paddings and work correctly
  box-sizing: border-box;
  overflow: auto;
  @include alpheios-border;
}

.alpheios-popup__header {
  display: flex;
  justify-content: space-between;
  height: px2rem(56px);
  background: var(--alpheios-color-muted);
}

.alpheios-popup__logo {
  position: relative;
  left: px2rem(16px);
  top: px2rem(18px);

  svg {
    width: px2rem(28px);
    height: auto;
  }
}

.alpheios-popup__close-btn {
  width: px2rem(80px);
  height: 100%;
  cursor: pointer;
  fill: var(--alpheios-color-neutral-lightest);
  stroke: var(--alpheios-color-neutral-lightest);
  stroke-width: 2.5;

  svg {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    left: px2rem(16px);
    width: px2rem(44px);
    height: auto;
  }

  &:hover,
  &:focus {
    fill: var(--alpheios-color-bright-hover);
    stroke: var(--alpheios-color-bright-hover);
    background: var(--alpheios-color-muted-hover);
  }

  &:active {
    fill: var(--alpheios-color-bright-pressed);
    stroke: var(--alpheios-color-bright-pressed);
    background: var(--alpheios-color-muted-pressed);
  }
}

.alpheios-popup__body {
  display: flex;
  flex-direction: column;
  padding: px2rem(20px);
  background: var(--alpheios-color-neutral-lightest);
}

.alpheios-popup__toolbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: px2rem(10px);
}

.alpheios-popup__toolbar-text {
  padding-right: px2rem(20px);
}

.alpheios-popup__toolbar-text[lang='ara'] {
  padding: 0px 20px 0px 20px; /* the arabic amiri font does not like the top padding */
}

h3.alpheios-popup__toolbar-selection {
  display: inline-block;
}

.alpheios-popup__toolbar-buttons {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.alpheios-popup__toolbar-button {
  margin: 0 0 px2rem(10px) px2rem(10px);
}

.alpheios-popup__content {
  padding: px2rem(20px);
  @include alpheios-border;
}

.alpheios-popup__providers {
  margin-left: px2rem(40px);
}

.alpheios-popup__providers-title {
  font-weight: 700;
  color: var(--alpheios-color-neutral-dark);
}

a.alpheios-popup__providers-link {
  display: inline-block;
  margin-bottom: px2rem(6px);
  font-weight: 700;
}

.alpheios-popup__definitions--placeholder {
  padding: px2rem(10px) 0;
}

.alpheios-popup__providers-item {
  color: var(--alpheios-color-neutral-dark);
}

.alpheios-popup__notifications,
.alpheios-popup__notifications-auth {
  display: none;
  position: relative;
  padding: px2rem(10px) px2rem(20px);
  background: $alpheios-logo-color;
  flex: 0 0 px2rem(60px);
  overflow: hidden;
}

.alpheios-popup__notifications-close-btn {
  position: absolute;
  right: px2rem(5px);
  top: px2rem(5px);
  display: block;
  width: px2rem(20px);
  height: px2rem(20px);
  margin: 0;
  cursor: pointer;
  fill: $alpheios-link-color-dark-bg;
  stroke: $alpheios-link-color-dark-bg;
}

.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: $alpheios-link-hover-color;
  stroke: $alpheios-link-hover-color;
}

[data-notification-visible="true"] .alpheios-popup__notifications {
  display: block;
}

[data-notification-auth-visible="true"] .alpheios-popup__notifications-auth {
  display: block;
}

.alpheios-popup__notifications--lang-switcher {
  font-size: 0.75*$alpheios-base-font-size;
  float: right;
  margin: px2rem(-20px) px2rem(10px) 0 0;
  display: inline-block;
}

.alpheios-popup__notifications--lang-switcher .alpheios-select {
  width: px2rem(120px);
  height: px2rem(25px);
}

.alpheios-popup__notifications--important {
  background: $alpheios-icon-color;
}
